












import Vue from 'vue'
import ApiTree from '@/components/api-tree/ApiTree.vue'
import BreadCrumb from '@/components/explorer/BreadCrumb.vue'

export default Vue.extend({
  components: {
    'api-tree': ApiTree,
    breadcrumb: BreadCrumb
  },
  computed: {
    loading(): boolean {
      return this.$store.state.ui.dataLoading
    }
  }
})



